@import url(https://fonts.googleapis.com/css?family=Poppins:300,400,500,600,700);
/*
    DEMO STYLE
*/
body {
    font-family: 'Poppins', sans-serif;
    background: #fafafa;
}

p {
    font-family: 'Poppins', sans-serif;
    font-size: 1.1em;
    font-weight: 300;
    line-height: 1.7em;
    color: #999;
}

a,
a:hover,
a:focus {
    color: inherit;
    text-decoration: none;
    transition: all 0.3s;
}

.navbar {
    padding: 15px 10px;
    background: #fff;
    border: none;
    border-radius: 0;
    margin-bottom: 40px;
    box-shadow: 1px 1px 3px rgba(0, 0, 0, 0.1);
}

.navbar-btn {
    box-shadow: none;
    outline: none !important;
    border: none;
}

.line {
    width: 100%;
    height: 1px;
    border-bottom: 1px dashed #ddd;
    margin: 40px 0;
}

/* ---------------------------------------------------
    SIDEBAR STYLE
----------------------------------------------------- */

.wrapper {
    display: flex;
    width: 100%;
    align-items: stretch;
}

#sidebar {
    min-width: 250px;
    max-width: 250px;
    background: #7386D5;
    color: #fff;
    transition: all 0.3s;
}

#sidebar.active {
    margin-left: -250px;
}

#sidebar .sidebar-header {
    padding: 20px;
    background: #6d7fcc;
}

#sidebar ul.components {
    padding: 20px 0;
    border-bottom: 1px solid #47748b;
}

#sidebar ul p {
    color: #fff;
    padding: 10px;
}

#sidebar ul li a {
    padding: 10px;
    font-size: 1.1em;
    display: block;
}

#sidebar ul li a:hover {
    color: #7386D5;
    background: #fff;
}

#sidebar ul li.active>a,
a[aria-expanded="true"] {
    color: #fff;
    background: #6d7fcc;
}

a[data-toggle="collapse"] {
    position: relative;
}

#iconBar ul li.active>a,
button[aria-expanded="true"] {
    color: #fff;
    background: #6d7fcc;
}

button[data-toggle="collapse"] {
    position: relative;
}

.dropdown-toggle::after {
    display: block;
    position: absolute;
    top: 50%;
    right: 20px;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
}

ul ul a {
    font-size: 0.9em !important;
    padding-left: 30px !important;
    background: #6d7fcc;
}

ul.CTAs {
    padding: 20px;
}

ul.CTAs a {
    text-align: center;
    font-size: 0.9em !important;
    display: block;
    border-radius: 5px;
    margin-bottom: 5px;
}

a.download {
    background: #fff;
    color: #7386D5;
}

a.article,
a.article:hover {
    background: #6d7fcc !important;
    color: #fff !important;
}

/* ---------------------------------------------------
    CONTENT STYLE
----------------------------------------------------- */

#content {
    width: 100%;
    padding: 20px;
    min-height: 100vh;
    transition: all 0.3s;
}

.content-center {
    margin: auto;
    width: 98%;
    background-color: #fff;
    padding: 5px;
    text-align: left;
}

/* ---------------------------------------------------
    MEDIAQUERIES
----------------------------------------------------- */

@media (max-width: 768px) {
    #sidebar {
        margin-left: -250px;
    }
    #sidebar.active {
        margin-left: 0;
    }
    #sidebarCollapse span {
        display: none;
    }
    #iconBar {
        display: block !important;
    }

    .dropdown-item{
        display:block;
        width:100%;
        padding:.25rem 1.5rem;
        clear:both;
        font-weight:400;
        color:#212529;
        text-align:inherit;
        white-space:nowrap;
        background-color:transparent;
        border:0;
        font-size: 2.00rem;
    }
    .dropdown-item:focus,.dropdown-item:hover{
        color:#16181b;
        text-decoration:none;
        background-color:#f8f9fa
    }
    .dropdown-item.active,.dropdown-item:active{
        color:#fff;
        text-decoration:none;
        background-color:#007bff
    }
    .dropdown-item.disabled,.dropdown-item:disabled{
        color:#6c757d;
        background-color:transparent
    }
    .dropdown-menu.show{
        display:block
    }
    .dropdown-header{
        display:block;
        padding:.5rem 1.5rem;
        margin-bottom:0;
        font-size:.875rem;
        color:#6c757d;
        white-space:nowrap
    }
    .dropdown-item-text{
        display:block;
        padding:.25rem 1.5rem;
        color:#212529
    }
}


